import { io } from "socket.io-client";

export const socket = io("https://snapcart-usja.onrender.com", {
  transports: ["websocket", "polling"],
});

// ‚úÖ Register user after login
export const registerUserSocket = (userEmail) => {
  if (userEmail) {
    socket.emit("register", userEmail);
    console.log("üü¢ Registered socket for:", userEmail);
  }
};

// ‚úÖ Unregister user on logout
export const unregisterUserSocket = (userEmail) => {
  if (userEmail) {
    socket.emit("unregister", userEmail);
    console.log("üî¥ Unregistered socket for:", userEmail);
  }
};
